  position: relative;
  min-height: 1px;
  padding-left: 15px;
  padding-right: 15px;
}
.content .areYouDoctorForm .formRow .formCol .form-group {
  margin-bottom: 15px;
}
.content .areYouDoctorForm .formRow .formCol .form-group label {
  color: #4a4a4a;
  margin-bottom: 10px;
  font-size: 20px;
  display: inline-block;
  max-width: 100%;
  font-weight: 700;
}
.content .areYouDoctorForm .formRow .formCol .form-group .formInputs {
  height: 56px;
  border-radius: 4px;
  background-color: #fff;
  /* width: 100%; */
  font-size: 20px;
  color: #4a4a4a;
  padding: 10px 16px;
  border: none;
}
.content .areYouDoctorForm .formRow .formCol .form-group .center {
  display: block;
  margin-left: auto;
