  text-indent: 15px;
  background-color: #ffffff;
  width: 50%;
  border-color: #ffffff;
}
.content .areYouDoctorForm .formRow .formCol {
  width: 100%;
}
.content .areYouDoctorForm .btn-lg {
  padding: 15px 16px;
  font-size: 25px;
  background-color: rgb(48, 195, 75);
  color: #fff;
  display: block;
  width: 100%;
}
